.navbar-header {
    position: sticky;
    /* height: 7vh; */
  }
  
  .logo{
    border: 4px solid white;
    border-radius: 10px;
    height: 40px;
    width: 124px;
    padding-left: 8px;
    padding-right: 8px;
    /* padding: auto; */
    justify-content: center;
    font-family: 'Trebuchet MS', sans-serif
  }
  
  .left {
    margin-left: 17rem;
  }
  
  .mid {
    margin: auto;
  }
  
  .nav_item {
    /* display: inline-block; */
    margin: auto;
  }
  
  .nav_item li {
    padding: 5px;
    display: inline-block;
    text-decoration: none;
    margin-left: 8px;
    margin-right: 8px;
    cursor: pointer;
  }
  
  /* .nav_item li:hover {
    background-color: white;
    color: rgb(212, 19, 19);
    border: 2px solid orange;
    border-radius: 25px;
    font-size: large;
  } */
  
  .search_image {
    height: 20px;
    justify-content: center;
  }
  
  .search {
    /* height: 10px; */
    display: flex;
    border: 2px solid rgb(255, 255, 255);
    padding: 0px;
    margin: auto;
    margin-inline-end: 25px;
    height: 40px;
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
  }
  
  /* .search_input {
    height: 38px;
    padding: 4px;
    padding-left: 12px;
    border-radius: none;
    margin: -1px;
    margin-right: 3px;
    border: 2px solid white;
    border-radius: 10px;
  }
  
  .search_input:focus {
    outline: none;
  } */
  
  .search_btn {
    /* margin: 0px;
    padding: auto; */
    height: 35px;
    width: 35px;
    background-color: orange;
    border: none;
    border-radius: 10px;
    justify-content: space-around;
  }
  
  .search_btn:hover {
    background-color: orange;
    border-radius: 50px;
  }
  
  .btn {
    height: 40px;
    float: right;
    background-color: orange;
    margin: 3px;
    margin-left: 5px;
    margin-right: 5px;
    width: 115px;
    border: 2px solid white;
    border-radius: 13px;
    color: black;
  }
  
  .btn:hover {
    border: 3px solid orange;
    background-color: white;
    width: 105px;
    height: 40px;
    border-radius: 19px;
    color: black;
  }

  .link{
    color: white;
    text-decoration: none;
  }
  
  .link:hover{
    padding: 5px;
    background-color: white;
    color: black;
    border: 2px solid orange;
    border-radius: 25px;
    font-size: large;
  }